@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);

#content-container{ 
    font-family: "SF Mono";
    height: 80vh;
    width: 80vw;
    margin: 0 auto;
    padding:20px;
    margin-top: 5%;

}

.intro_hello{
    font-family: "Open Sans";
    color: rgb(33, 172, 166);
    font-size: 22px;
}

.banner_main {
    font-family: 'Andale Mono';
    position: fixed;
    top: 0;
    margin: 0;
    width: 95%;
    padding: 20px;
    padding-bottom: 2px;
    /* border-bottom: 1px solid bisque; */
    
}
#heading { 
    
    font-size: 2.5em;
    color: rgb(33, 172, 166);
}
#heading>a> .blinking{
    color: rgb(33, 172, 166) !important;
}

#heading > a {
    text-decoration: none;
    color: rgb(33, 172, 166) !important;
    border:1px solid;
    padding: 5px;
}
.banner_items { 
    display: inline;
    float: right;
    margin-right: 20px;
}
.banner_items > li{
    list-style: none;
    display: inline;
    margin-right: 40px;
    color: rgb(33, 172, 166);
    cursor: pointer;
}
.banner_items > li > a {
    text-decoration: none;
    color: rgb(33, 172, 166) !important;
}
.banner_items> li:hover{
    color:white;
    
    transition: 0.4s ease-in;
}
.blinking{
    -webkit-animation:blinkingTextWhite 2s infinite;
            animation:blinkingTextWhite 2s infinite;
}
@-webkit-keyframes blinkingTextWhite {
    60% {
        opacity: 0.1;
        }
    
}
@keyframes blinkingTextWhite {
    60% {
        opacity: 0.1;
        }
    
}
@-webkit-keyframes blinkingText{
    0%{     color: #000;    }
    49%{    color: #000; }
    60%{    color: transparent; }
    99%{    color:transparent;  }
    100%{   color: #000;    }
}
@keyframes blinkingText{
    0%{     color: #000;    }
    49%{    color: #000; }
    60%{    color: transparent; }
    99%{    color:transparent;  }
    100%{   color: #000;    }
}

.button{
    color :rgb(33, 172, 166);
    border: 1px solid rgb(33, 172, 166);
    border-radius: 4px;
    padding: 10px;
    cursor: pointer;
    font-size: 13px;
    font-weight: 600;

    box-shadow: 1px 2px 3px black;
    transition: 0.2s ease-in-out;
}

.button:hover {
    color: white;
    box-shadow: none;
}
.logo_line { 
    margin-top: 5%;
}
.logo_item{
    list-style: none;
    display: inline;
}

.logo_image{
    width: 50px;
    margin-left: 5%;
    margin-right: 5%;
}
.name {
    font-size: 80px;
    color: rgb(204, 214, 246);
    margin-left: 20px;
    line-height: 1.2;
    font-family: Open Sans;

}


.who_am_i_desc{
    font-family: "Open Sans", "sans-serif";
    font-size: 40px;
    color: #aaa;
    margin-left:20px;
}

.image_container > img{
    
    border:1px solid #ddd;
    /* box-shadow: 1px 2px 3px 0px #aaa; */
    box-shadow: 1 1 1px 0px white inset, 0 0 1px 0px white;
    max-width: 100%;
    height: auto;
    position: relative;
    left: 20px;
    top: 20px;
    
}
.image_container {
  
    border: 1px solid;
    
    z-index: -1;
}
.img-hover-zoom {
    height: 300px; /* [1.1] Set it as per your need */
    overflow: hidden; /* [1.2] Hide the overflowing of child elements */
  }
  
  /* [2] Transition property for smooth transformation of images */
  .img-hover-zoom img {
    transition: transform .5s ease;
  }
  
  /* [3] Finally, transforming the image when container gets hovered */
  .img-hover-zoom:hover img {
    transform: scale(1.5);
  }
  
.about {
    font-family: "Jetbrains Mono" , "sans-serif";
    color: rgb(33, 172, 166);
    font-weight: 600;
    height: 80vh;
    margin-left: 10vw;
    width: 60vw;
    /* margin: 0 auto; */
}
.about_divide{
    font-weight: 400;
    color:white;
    display: flex;
}

.about_content{
    flex: 60% 1;
}
.about_sep{
    flex:10% 1;
}
.about_pic {
    flex:30% 1;
}
body {
  margin: 0;
  background-color: rgb(10, 25, 47);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  display: flex;
  flex-direction: column;
  margin-top: 10vw;
}

.contianer > div{
  flex:1 1;
}
